import BaseApiService from "services/BaseApiService";

const DEFAULT_PATH = "/custom/api/industry/type";

class IndustryService extends BaseApiService {
  getAll = (start = 0, limit = 10, search = "") => {
    const url = `${DEFAULT_PATH}/search?${search}start=${start}&limit=${limit}`;
    return this.makeGetRequestWithAuth(url);
  };
}

export default new IndustryService();
